#root {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  overflow: hidden auto;
}

#modals {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: max-content;
  /* max-height: 100vh; */
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: scroll;
  /* overflow: hidden auto; */
}

button:not(:disabled),
select:not(:disabled) {
  cursor: pointer;
}
