.profile-list {
  position: static;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 100%;
  max-width: 45rem;
  min-height: max-content;

  // height: auto;

  @media (max-width: 592px) {
    margin: 0 0 2rem;
    padding: 0;
  }

  h2 {
    margin: 0 0 1rem;
  }

  p {
    margin: 0 0 1rem;
  }
}
